body {
  margin: 0;
  font-family: Arial Nova;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
 
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
 
.content {
  padding: 20px;
}
 
.header {
  padding: 10px;
  background: rgb(255,255,255);
  border-bottom: 1px solid #999;
  height: 50px;
}
.header a {
  color: #F63236;
  text-decoration: none;
  margin-left: 20px;
  margin-right: 5px;
}
.header a:hover {
  color: #8a0f53;
}
.header small {
  color: #666;
}
.header .active {
  color: #2c7613;
}

.logo {
	position: absolute;
	left: 5px;
	top: 0px;
	width: 100px;
}

.pWelcome {
	width: 50%;
}

.notes {
	font-size: 14px;
}

.notes-small {
	font-size: 10px;
}

.radio-small {
	font-size: 12px;
	vertical-align: text-middle;
}

.signature-container {
  border: solid 1px #ccc;
}

.float-container {
  border: solid 1px #ccc;
  position: relative;
  width: 200px;
}
.float-container input {
  border: none;
  outline: 0;
}
label {
  position: absolute;
  margin-top: 5px;
  font-size: 14px;
  color: gray
}

.report-container {
	width: 1000px;
	height: 500px;
	overflow: auto;
}

.report-page{
	border-style: solid;
	border-color: GhostWhite;
	height: 1200px;
	page-break-after: always;
}

.report-page-inner{
	margin-top: 20px;
	width: 90%;
}

.report-page-section{
	width: 100%;
}

.report-table{
	width: 100%;
}

.std-input {
	width: 250px;
}

.long-input {
	width: 550px;
}

.std-label {
	font-size: 16px;
}

.report-notes-s {
	font-size: 12px;
}

.report-notes-m {
	font-size: 14px;
}

.report-notes-l {
	font-size: 18px;
}

.report-header-l {
  color: #F63236;
  font-size: 36px;
}

.report-header-m {
  color: #F63236;
  font-size: 28px;
}

.report-logo {
	width: 100px;
}

.report-valign-bottom {
	vertical-align: bottom;
}

.hr-red {
  border-top: 2px solid red;
}

.hr-gray {
  border-top: 1px solid gray;
}

.divInfo {
	width: 97%;
	position: absolute;
	top: 50px;
	visibility: hidden;
}

.divBlur {
  background-color: gray;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 3500px;
  opacity: 0.8;
  visibility: hidden;
}

